<template>
  <div class="login-box-wrap">
    <dialog title="登 录" class="login-box" :class="{ shake: loginFailed }">
      <div slot="body" class="login-box-content">
        <input type="text" v-model="username" placeholder="用户名">
        <input type="password" v-model="password" placeholder="密码">
        <button class="btn" @click="login">登 录</button>
      </div>
    </dialog>
  </div>
</template>

<script>
  import dialog from '../components/dialog.vue';
  const USERS = [
    { username: 'wtmanutd', password: '123456' },
    { username: 'xixidong', password: '123456' },
  ];

  export default {
    components: {
      dialog,
    },

    props: [],

    data() {
      return {
        username: '',
        password: '',
        loginFailed: false,
        submitting: false,
      };
    },

    computed: {},

    methods: {
      login() {
        const success = USERS.some(
          (user) => user.username === this.username && user.password === this.password
        );

        if (success) {
          this.$dispatch('login-success', this.username);
        } else {
          this.loginFailed = true;
          setTimeout(() => (this.loginFailed = false), 1000);
        }
      },
    },

    ready() {},
  };
</script>

<style scoped>
.login-box-wrap {
  font-size: 16px;
  position: fixed;
  width: 400px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.login-box {
  animation-duration: 1s;
}

.login-box-content {
  padding: 2em 3em;
}

input {
  display: block;
  width: 100%;
  padding: 1em 2em;
  margin-bottom: 2em;
  border: 1px solid #28c6df;
  border-radius: 2em;
  background: transparent;
  outline: none;
}

button {
  display: block;
  width: 100%;
  padding: 1em;
  margin-bottom: 2em;
  border-radius: 2em;
}


@keyframes shake {
  10%, 30%, 50%, 70%, 90% {
    transform: translate(-10px, 0);
  }

  20%, 40%, 60%, 80% {
    transform: translate(10px, 0);
  }
}

.shake {
  animation-name: shake;
}
</style>



